import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { useAuthStore } from '../store/authStore'
import { useBacktestStore, useBacktestList, useBacktestCreation, useBacktestComparison } from '../store/backtestStore'
import { BacktestResultChart } from '../components/charts'
import { LoadingSpinner, Button } from '../components/common'
import { formatDateTime, formatCurrency, formatPercent } from '../utils/format'

const BacktestPage: React.FC = () => {
  const { user } = useAuthStore()
  const [activeTab, setActiveTab] = useState<'create' | 'history'>('create')
  
  // 使用状态管理hooks
  const { backtests, loading, error, fetchBacktests, deleteBacktest, downloadReport } = useBacktestList()
  const { configForm, isCreatingBacktest, createBacktest, updateBacktestForm, resetBacktestForm } = useBacktestCreation()
  const { selectedForComparison, toggleComparisonSelection, compareBacktests, clearComparison } = useBacktestComparison()
  
  // 加载数据
  useEffect(() => {
    fetchBacktests()
  }, [])
  
  // 处理回测创建
  const handleCreateBacktest = async () => {
    if (!configForm.strategy_id) {
      alert('请选择策略')
      return
    }
    
    const result = await createBacktest(configForm)
    if (result) {
      setActiveTab('history') // 创建成功后切换到历史记录
    }
  }
  
  // 处理删除回测
  const handleDeleteBacktest = async (id: string) => {
    if (confirm('确定要删除这个回测吗？')) {
      await deleteBacktest(id)
    }
  }
  
  // 状态样式映射
  const getStatusStyle = (status: string) => {
    switch (status) {
      case 'completed':
        return 'bg-green-100 text-green-800'
      case 'running':
        return 'bg-yellow-100 text-yellow-800'
      case 'failed':
        return 'bg-red-100 text-red-800'
      default:
        return 'bg-gray-100 text-gray-800'
    }
  }
  
  const getStatusText = (status: string) => {
    switch (status) {
      case 'completed': return '已完成'
      case 'running': return '运行中'
      case 'failed': return '已失败'
      default: return '未知'
    }
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* 页面容器 */}
      <div className="w-full max-w-6xl min-h-screen bg-white rounded-xl shadow-xl border border-gray-200 mx-auto">
        {/* 头部导航 */}
        <header className="py-4 px-8 flex justify-between items-center border-b border-gray-200">
          <div className="flex items-center">
            <svg
              className="w-7 h-7 mr-3"
              viewBox="0 0 24 24"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <circle cx="12" cy="12" r="10" stroke="#1a3d7c" strokeWidth="2" fill="none"/>
              <path d="M8 12h8M12 8v8" stroke="#1a3d7c" strokeWidth="2"/>
            </svg>
            <h1 className="text-xl font-bold" style={{ color: '#1a3d7c' }}>
              Trademe
            </h1>
          </div>
          
          <nav className="flex gap-2">
            <Link to="/" className="px-4 py-2 rounded-lg text-gray-700 hover:bg-gray-100 transition-colors">
              首页
            </Link>
            <Link to="/strategies" className="px-4 py-2 rounded-lg text-gray-700 hover:bg-gray-100 transition-colors">
              策略交易
            </Link>
            <Link to="/trading" className="px-4 py-2 rounded-lg text-gray-700 hover:bg-gray-100 transition-colors">
              图表交易
            </Link>
            <Link to="/api-keys" className="px-4 py-2 rounded-lg text-gray-700 hover:bg-gray-100 transition-colors">
              API管理
            </Link>
            <Link to="/trading-notes" className="px-4 py-2 rounded-lg text-gray-700 hover:bg-gray-100 transition-colors">
              交易心得
            </Link>
            <Link to="/profile" className="px-4 py-2 rounded-lg text-gray-700 hover:bg-gray-100 transition-colors">
              账户中心
            </Link>
          </nav>
          
          <div className="flex items-center">
            <div className="w-9 h-9 rounded-full bg-gray-200 overflow-hidden">
              <div className="w-full h-full bg-gradient-to-r from-blue-400 to-blue-600 flex items-center justify-center text-white font-semibold">
                {user?.username?.charAt(0).toUpperCase()}
              </div>
            </div>
          </div>
        </header>

        {/* 主要内容 */}
        <main className="p-8">
          {/* 选项卡 */}
          <section className="mb-8">
            <div className="flex border-b border-gray-200">
              <button 
                onClick={() => setActiveTab('create')}
                className={`px-6 py-3 font-medium border-b-2 transition-colors ${
                  activeTab === 'create' 
                    ? 'border-blue-600 text-blue-600' 
                    : 'border-transparent text-gray-500 hover:text-gray-700'
                }`}
              >
                创建回测
              </button>
              <button 
                onClick={() => setActiveTab('history')}
                className={`px-6 py-3 font-medium border-b-2 transition-colors ${
                  activeTab === 'history' 
                    ? 'border-blue-600 text-blue-600' 
                    : 'border-transparent text-gray-500 hover:text-gray-700'
                }`}
              >
                回测历史
              </button>
            </div>
          </section>

          {activeTab === 'create' ? (
            /* 创建回测 */
            <section className="grid grid-cols-2 gap-8">
              {/* 左侧：回测配置 */}
              <div className="bg-white rounded-xl shadow-sm border border-gray-100 p-6">
                <h2 className="text-xl font-bold mb-6" style={{ color: '#1a3d7c' }}>回测配置</h2>
                
                <div className="space-y-6">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">
                      选择策略 *
                    </label>
                    <select 
                      value={configForm.strategy_id}
                      onChange={(e) => updateBacktestForm('strategy_id', e.target.value)}
                      className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                      required
                    >
                      <option value="">请选择策略</option>
                      <option value="rsi_strategy">RSI 均值回归策略</option>
                      <option value="macd_strategy">MACD 趋势跟踪</option>
                      <option value="grid_strategy">网格交易策略</option>
                      <option value="ema_crossover">EMA 交叉策略</option>
                      <option value="bollinger_strategy">布林带突破策略</option>
                    </select>
                  </div>
                  
                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-2">
                        交易对
                      </label>
                      <select 
                        value={configForm.symbol}
                        onChange={(e) => updateBacktestForm('symbol', e.target.value)}
                        className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                      >
                        <option value="BTC/USDT">BTC/USDT</option>
                        <option value="ETH/USDT">ETH/USDT</option>
                        <option value="BNB/USDT">BNB/USDT</option>
                        <option value="SOL/USDT">SOL/USDT</option>
                        <option value="ADA/USDT">ADA/USDT</option>
                      </select>
                    </div>
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-2">
                        交易所
                      </label>
                      <select 
                        value={configForm.exchange}
                        onChange={(e) => updateBacktestForm('exchange', e.target.value)}
                        className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                      >
                        <option value="binance">币安 (Binance)</option>
                        <option value="okx">OKX</option>
                        <option value="huobi">火币 (Huobi)</option>
                      </select>
                    </div>
                  </div>
                  
                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-2">
                        开始日期
                      </label>
                      <input 
                        type="date" 
                        value={configForm.start_date}
                        onChange={(e) => updateBacktestForm('start_date', e.target.value)}
                        className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                      />
                    </div>
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-2">
                        结束日期
                      </label>
                      <input 
                        type="date" 
                        value={configForm.end_date}
                        onChange={(e) => updateBacktestForm('end_date', e.target.value)}
                        className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                      />
                    </div>
                  </div>
                  
                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-2">
                        初始资金 (USDT)
                      </label>
                      <input 
                        type="number" 
                        value={configForm.initial_capital}
                        onChange={(e) => updateBacktestForm('initial_capital', Number(e.target.value))}
                        min="100"
                        step="100"
                        className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                      />
                    </div>
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-2">
                        时间框架
                      </label>
                      <select 
                        value={configForm.timeframe}
                        onChange={(e) => updateBacktestForm('timeframe', e.target.value)}
                        className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                      >
                        <option value="1m">1分钟</option>
                        <option value="5m">5分钟</option>
                        <option value="15m">15分钟</option>
                        <option value="1h">1小时</option>
                        <option value="4h">4小时</option>
                        <option value="1d">1天</option>
                      </select>
                    </div>
                  </div>
                  
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">
                      手续费率 (%)
                    </label>
                    <input 
                      type="number" 
                      value={configForm.commission_rate * 100}
                      onChange={(e) => updateBacktestForm('commission_rate', Number(e.target.value) / 100)}
                      step="0.001"
                      min="0"
                      max="1"
                      className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                    />
                    <p className="text-sm text-gray-500 mt-1">币安 Maker: 0.1%, Taker: 0.1%</p>
                  </div>
                  
                  <div className="flex gap-3">
                    <button 
                      onClick={handleCreateBacktest}
                      disabled={isCreatingBacktest || !configForm.strategy_id}
                      className="flex-1 py-3 rounded-lg text-white font-medium transition-all hover:opacity-90 disabled:opacity-50 disabled:cursor-not-allowed flex items-center justify-center"
                      style={{ backgroundColor: '#1a3d7c' }}
                    >
                      {isCreatingBacktest ? (
                        <>
                          <LoadingSpinner size="sm" className="mr-2" />
                          创建中...
                        </>
                      ) : (
                        '开始回测'
                      )}
                    </button>
                    <button 
                      onClick={resetBacktestForm}
                      className="px-4 py-3 rounded-lg border border-gray-300 text-gray-700 font-medium transition-all hover:bg-gray-50"
                    >
                      重置
                    </button>
                  </div>
                  
                  {error && (
                    <div className="p-4 bg-red-50 border border-red-200 rounded-lg">
                      <p className="text-red-700 text-sm">{error}</p>
                    </div>
                  )}
                </div>
              </div>

              {/* 右侧：实时结果预览 */}
              <div className="bg-white rounded-xl shadow-sm border border-gray-100 p-6">
                <h2 className="text-xl font-bold mb-6" style={{ color: '#1a3d7c' }}>回测预览</h2>
                
                {isCreatingBacktest ? (
                  <div className="flex flex-col items-center justify-center h-80">
                    <LoadingSpinner size="lg" className="mb-4" />
                    <p className="text-gray-600">正在执行回测...</p>
                    <p className="text-sm text-gray-500 mt-2">预计需要 30-60 秒</p>
                  </div>
                ) : backtests.length > 0 ? (
                  <BacktestResultChart 
                    metrics={{
                      total_return: backtests[0].results?.total_return || 0,
                      max_drawdown: backtests[0].results?.max_drawdown || 0,
                      sharpe_ratio: backtests[0].results?.sharpe_ratio || 0,
                      win_rate: backtests[0].results?.win_rate || 0,
                      profit_factor: backtests[0].results?.profit_factor || 0,
                      annual_return: backtests[0].results?.annual_return || 0,
                      volatility: backtests[0].results?.volatility || 0,
                      total_trades: backtests[0].results?.total_trades || 0
                    }}
                    equityCurve={backtests[0].results?.equity_curve?.map(point => ({
                      timestamp: point.timestamp,
                      equity: point.value,
                      drawdown: 0 // 需要计算
                    })) || []}
                    trades={backtests[0].results?.trade_history || []}
                    height={300}
                  />
                ) : (
                  <div className="flex flex-col items-center justify-center h-80 text-gray-500">
                    <div className="w-16 h-16 bg-gray-100 rounded-full flex items-center justify-center mb-4">
                      <svg className="w-8 h-8 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 19v-6a2 2 0 00-2-2H5a2 2 0 00-2 2v6a2 2 0 002 2h2a2 2 0 002-2zm0 0V9a2 2 0 012-2h2a2 2 0 012 2v10m-6 0a2 2 0 002 2h2a2 2 0 002-2m0 0V5a2 2 0 012-2h2a2 2 0 012 2v14a2 2 0 01-2 2h-2a2 2 0 01-2-2z" />
                      </svg>
                    </div>
                    <p>选择策略并开始回测</p>
                    <p className="text-sm mt-1">回测结果将在此处显示</p>
                  </div>
                )}
              </div>
            </section>
          ) : (
            /* 回测历史 */
            <section className="bg-white rounded-xl shadow-sm border border-gray-100">
              <div className="p-6 border-b border-gray-100 flex justify-between items-center">
                <h2 className="text-xl font-bold" style={{ color: '#1a3d7c' }}>回测历史</h2>
                <button 
                  className="px-4 py-2 rounded-lg text-white font-medium transition-all hover:opacity-90"
                  style={{ backgroundColor: '#1a3d7c' }}
                  onClick={() => setActiveTab('create')}
                >
                  新建回测
                </button>
              </div>
              
              <div className="overflow-x-auto">
                {loading ? (
                  <div className="flex justify-center items-center h-64">
                    <LoadingSpinner size="lg" />
                  </div>
                ) : backtests.length === 0 ? (
                  <div className="text-center py-16">
                    <div className="w-16 h-16 bg-gray-100 rounded-full flex items-center justify-center mx-auto mb-4">
                      <svg className="w-8 h-8 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5H7a2 2 0 00-2 2v10a2 2 0 002 2h8a2 2 0 002-2V7a2 2 0 00-2-2h-2M9 5a2 2 0 002 2h2a2 2 0 002-2M9 5a2 2 0 012-2h2a2 2 0 012 2" />
                      </svg>
                    </div>
                    <h3 className="text-lg font-medium text-gray-900 mb-2">暂无回测记录</h3>
                    <p className="text-gray-500 mb-4">创建您的第一个回测以开始使用</p>
                    <button 
                      onClick={() => setActiveTab('create')}
                      className="px-4 py-2 rounded-lg text-white font-medium transition-all hover:opacity-90"
                      style={{ backgroundColor: '#1a3d7c' }}
                    >
                      创建回测
                    </button>
                  </div>
                ) : (
                  <table className="w-full">
                    <thead className="bg-gray-50">
                      <tr>
                        <th className="px-2 py-3 text-left">
                          <input 
                            type="checkbox" 
                            className="rounded border-gray-300"
                            onChange={(e) => {
                              if (e.target.checked) {
                                backtests.forEach(bt => toggleComparisonSelection(bt.id))
                              } else {
                                clearComparison()
                              }
                            }}
                          />
                        </th>
                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                          策略ID
                        </th>
                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                          交易对
                        </th>
                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                          时间范围
                        </th>
                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                          总收益率
                        </th>
                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                          最大回撤
                        </th>
                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                          夏普比率
                        </th>
                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                          状态
                        </th>
                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                          操作
                        </th>
                      </tr>
                    </thead>
                    <tbody className="bg-white divide-y divide-gray-200">
                      {backtests.map((backtest) => (
                        <tr key={backtest.id} className="hover:bg-gray-50">
                          <td className="px-2 py-4">
                            <input 
                              type="checkbox" 
                              className="rounded border-gray-300"
                              checked={selectedForComparison.includes(backtest.id)}
                              onChange={() => toggleComparisonSelection(backtest.id)}
                            />
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap">
                            <div className="text-sm font-medium text-gray-900">
                              {backtest.strategy_id}
                            </div>
                            <div className="text-xs text-gray-500">
                              {formatDateTime(backtest.created_at)}
                            </div>
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                            {backtest.config.symbol}
                            <div className="text-xs text-gray-400">{backtest.config.exchange}</div>
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                            {backtest.config.start_date} 至 {backtest.config.end_date}
                            <div className="text-xs text-gray-400">{backtest.config.timeframe}</div>
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap">
                            <span className={`font-semibold ${
                              (backtest.results?.total_return || 0) >= 0 ? 'text-green-600' : 'text-red-600'
                            }`}>
                              {formatPercent(backtest.results?.total_return || 0)}
                            </span>
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap">
                            <span className="text-red-600 font-semibold">
                              {formatPercent(backtest.results?.max_drawdown || 0)}
                            </span>
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap">
                            <span className="text-blue-600 font-semibold">
                              {backtest.results?.sharpe_ratio?.toFixed(2) || 'N/A'}
                            </span>
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap">
                            <span className={`inline-flex px-2 py-1 text-xs font-semibold rounded-full ${getStatusStyle(backtest.status)}`}>
                              {getStatusText(backtest.status)}
                            </span>
                          </td>
                          <td className="px-6 py-4 whitespace-nowrap text-sm space-x-2">
                            <button 
                              onClick={() => console.log('View details:', backtest.id)}
                              className="px-3 py-1 rounded bg-blue-100 text-blue-700 hover:bg-blue-200 transition-colors"
                            >
                              查看
                            </button>
                            <button 
                              onClick={() => downloadReport(backtest.id, 'html')}
                              className="px-3 py-1 rounded bg-gray-100 text-gray-700 hover:bg-gray-200 transition-colors"
                            >
                              下载
                            </button>
                            <button 
                              onClick={() => handleDeleteBacktest(backtest.id)}
                              className="px-3 py-1 rounded bg-red-100 text-red-700 hover:bg-red-200 transition-colors"
                            >
                              删除
                            </button>
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                )}
                
                {/* 回测比较功能 */}
                {selectedForComparison.length >= 2 && (
                  <div className="mt-4 p-4 bg-blue-50 border border-blue-200 rounded-lg">
                    <div className="flex items-center justify-between">
                      <p className="text-blue-700">
                        已选择 {selectedForComparison.length} 个回测进行比较
                      </p>
                      <div className="flex gap-2">
                        <button 
                          onClick={compareBacktests}
                          className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
                        >
                          比较回测
                        </button>
                        <button 
                          onClick={clearComparison}
                          className="px-4 py-2 bg-gray-500 text-white rounded-lg hover:bg-gray-600 transition-colors"
                        >
                          清除选择
                        </button>
                      </div>
                    </div>
                  </div>
                )}
              </div>
              
              {error && (
                <div className="mt-4 mx-6 mb-6 p-4 bg-red-50 border border-red-200 rounded-lg">
                  <p className="text-red-700 text-sm">{error}</p>
                </div>
              )}
            </section>
          )}
        </main>
      </div>
    </div>
  )
}

export default BacktestPage