import React, { ReactNode } from 'react'
import { Link, useLocation } from 'react-router-dom'
import { useAuthStore } from '../../store/authStore'

interface MainLayoutProps {
  children: ReactNode
}

const MainLayout: React.FC<MainLayoutProps> = ({ children }) => {
  const location = useLocation()
  const { user, logout } = useAuthStore()

  const navItems = [
    { path: '/', label: '首页', key: 'home' },
    { path: '/strategies', label: '策略交易', key: 'strategies' },
    { path: '/trading', label: '图表交易', key: 'trading' },
    { path: '/api-keys', label: 'API管理', key: 'api' },
    { path: '/insights', label: '交易心得', key: 'insights' },
    { path: '/profile', label: '账户中心', key: 'profile' },
  ]

  const isActiveRoute = (path: string) => {
    if (path === '/') {
      return location.pathname === '/'
    }
    return location.pathname.startsWith(path)
  }

  const handleLogout = () => {
    logout()
    window.location.href = '/login'
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* 页面容器 */}
      <div className="max-w-[1440px] mx-auto bg-white rounded-xl shadow-lg border border-gray-200 overflow-hidden relative">
        {/* Header */}
        <header className="py-4 px-8 flex justify-between border-b border-gray-200">
          {/* Logo */}
          <div className="flex items-center">
            <div className="w-7 h-7 rounded-full bg-brand-500 flex items-center justify-center mr-3">
              <span className="text-white font-bold text-sm">T</span>
            </div>
            <h1 className="text-xl font-bold text-brand-500">Trademe</h1>
          </div>
          
          {/* Navigation */}
          <nav className="flex gap-2">
            {navItems.map((item) => (
              <Link
                key={item.key}
                to={item.path}
                className={`nav-item ${
                  isActiveRoute(item.path) ? 'active' : ''
                }`}
              >
                {item.label}
              </Link>
            ))}
          </nav>
          
          {/* User Profile */}
          <div className="flex items-center relative group">
            <div className="w-9 h-9 rounded-full bg-gray-200 overflow-hidden cursor-pointer">
              {user?.avatar_url ? (
                <img 
                  src={user.avatar_url} 
                  alt="User avatar" 
                  className="w-full h-full object-cover"
                />
              ) : (
                <div className="w-full h-full flex items-center justify-center bg-brand-100 text-brand-600 font-medium">
                  {user?.username?.charAt(0).toUpperCase() || 'U'}
                </div>
              )}
            </div>
            
            {/* 用户下拉菜单 */}
            <div className="absolute top-full right-0 mt-2 w-48 bg-white rounded-lg shadow-lg border border-gray-200 py-2 opacity-0 invisible group-hover:opacity-100 group-hover:visible transition-all duration-200 z-50">
              <div className="px-4 py-2 border-b border-gray-100">
                <p className="font-medium text-gray-900">{user?.username}</p>
                <p className="text-sm text-gray-500">{user?.email}</p>
                <span className="inline-flex items-center px-2 py-1 rounded-full text-xs bg-blue-100 text-blue-800 mt-1">
                  {user?.membership_level === 'basic' && '基础版'}
                  {user?.membership_level === 'premium' && '高级版'}
                  {user?.membership_level === 'professional' && '专业版'}
                </span>
              </div>
              <Link
                to="/profile"
                className="block px-4 py-2 text-sm text-gray-700 hover:bg-gray-50"
              >
                个人设置
              </Link>
              <Link
                to="/profile/membership"
                className="block px-4 py-2 text-sm text-gray-700 hover:bg-gray-50"
              >
                会员管理
              </Link>
              <button
                onClick={handleLogout}
                className="block w-full text-left px-4 py-2 text-sm text-red-600 hover:bg-red-50"
              >
                退出登录
              </button>
            </div>
          </div>
        </header>
        
        {/* 语言和主题切换按钮 */}
        <div className="absolute top-4 right-8 flex items-center space-x-4 z-40">
          <div className="flex items-center space-x-2">
            <button 
              className="lang-toggle px-2 py-1 text-sm rounded-md bg-gray-100 hover:bg-gray-200"
              data-lang="zh"
            >
              中
            </button>
            <button 
              className="lang-toggle px-2 py-1 text-sm rounded-md hover:bg-gray-200"
              data-lang="en"
            >
              EN
            </button>
          </div>
          <div className="border-l border-gray-300 h-6"></div>
          <button 
            id="theme-toggle" 
            className="p-2 rounded-full hover:bg-gray-200"
            title="切换主题"
          >
            <svg width="20" height="20" viewBox="0 0 24 24">
              <path 
                fill="currentColor" 
                d="M12 2.25a.75.75 0 01.75.75v2.25a.75.75 0 01-1.5 0V3a.75.75 0 01.75-.75zM7.5 12a4.5 4.5 0 119 0 4.5 4.5 0 01-9 0zM18.894 6.166a.75.75 0 00-1.06-1.06l-1.591 1.59a.75.75 0 101.06 1.061l1.591-1.59zM21.75 12a.75.75 0 01-.75.75h-2.25a.75.75 0 010-1.5H21a.75.75 0 01.75.75zM17.834 18.894a.75.75 0 001.06-1.06l-1.59-1.591a.75.75 0 10-1.061 1.06l1.59 1.591zM12 18a.75.75 0 01.75.75V21a.75.75 0 01-1.5 0v-2.25A.75.75 0 0112 18zM7.758 17.303a.75.75 0 00-1.061-1.06l-1.591 1.59a.75.75 0 001.06 1.061l1.591-1.59zM6 12a.75.75 0 01-.75.75H3a.75.75 0 010-1.5h2.25A.75.75 0 016 12zM6.697 7.757a.75.75 0 001.06-1.06l-1.59-1.591a.75.75 0 00-1.061 1.06l1.59 1.591z"
              />
            </svg>
          </button>
        </div>
        
        {/* Main Content */}
        <main className="p-8">
          {children}
        </main>
      </div>
    </div>
  )
}

export default MainLayout