import React, { useState, useEffect, useRef } from 'react'
import { useNavigate } from 'react-router-dom'
import { useUserInfo, useWebSocketStatus, useGlobalLoading, useAIStore } from '../store'
import toast from 'react-hot-toast'
import { formatDateTime } from '@/utils/format'

interface Message {
  id: string
  type: 'user' | 'assistant'
  content: string
  timestamp: Date
  isLoading?: boolean
}

const AIChatPage: React.FC = () => {
  const navigate = useNavigate()
  const { user, isPremium } = useUserInfo()
  const { isConnected } = useWebSocketStatus()
  const { isLoading } = useGlobalLoading()
  
  const [messages, setMessages] = useState<Message[]>([
    {
      id: '1',
      type: 'assistant',
      content: '您好！我是Trademe AI交易助手。我可以帮您：\n\n• 📈 分析市场趋势和技术指标\n• 🤖 生成量化交易策略代码\n• 💡 提供交易建议和风险评估\n• 📊 解读回测结果和优化建议\n\n请告诉我您需要什么帮助？',
      timestamp: new Date(Date.now() - 5000)
    }
  ])
  
  const [inputValue, setInputValue] = useState('')
  const [isTyping, setIsTyping] = useState(false)
  const messagesEndRef = useRef<HTMLDivElement>(null)
  const inputRef = useRef<HTMLTextAreaElement>(null)
  
  // 自动滚动到最新消息
  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' })
  }
  
  useEffect(() => {
    scrollToBottom()
  }, [messages])
  
  // 处理发送消息
  const handleSendMessage = async () => {
    if (!inputValue.trim()) return
    
    if (!isPremium) {
      toast.error('AI对话功能需要升级到高级版本')
      return
    }
    
    const userMessage: Message = {
      id: Date.now().toString(),
      type: 'user',
      content: inputValue.trim(),
      timestamp: new Date()
    }
    
    setMessages(prev => [...prev, userMessage])
    setInputValue('')
    setIsTyping(true)
    
    // 模拟AI回复
    const loadingMessage: Message = {
      id: (Date.now() + 1).toString(),
      type: 'assistant',
      content: '',
      timestamp: new Date(),
      isLoading: true
    }
    
    setMessages(prev => [...prev, loadingMessage])
    
    // 模拟AI处理时间
    setTimeout(() => {
      const aiResponse = generateAIResponse(userMessage.content)
      setMessages(prev => prev.map(msg => 
        msg.id === loadingMessage.id 
          ? { ...msg, content: aiResponse, isLoading: false }
          : msg
      ))
      setIsTyping(false)
    }, 2000 + Math.random() * 2000)
  }
  
  // 生成模拟AI回复
  const generateAIResponse = (userInput: string): string => {
    const input = userInput.toLowerCase()
    
    if (input.includes('策略') || input.includes('代码')) {
      return `我可以帮您生成交易策略。这里是一个基于RSI指标的策略示例：

\`\`\`python
def rsi_strategy(data, rsi_period=14, oversold=30, overbought=70):
    # 计算RSI指标
    rsi = calculate_rsi(data['close'], rsi_period)
    
    # 生成信号
    signals = []
    for i in range(len(data)):
        if rsi[i] < oversold:
            signals.append('BUY')  # 超卖买入
        elif rsi[i] > overbought:
            signals.append('SELL')  # 超买卖出
        else:
            signals.append('HOLD')  # 持有
    
    return signals
\`\`\`

这个策略使用RSI指标识别超买超卖机会。您需要根据具体市场调整参数。需要我解释更多细节吗？`
    }
    
    if (input.includes('分析') || input.includes('市场')) {
      return `基于当前市场情况，我观察到以下趋势：

📊 **技术分析**
• BTC当前处于震荡区间，支撑位在$42,000
• ETH相对强势，可能突破$2,600阻力位
• 整体市场情绪偏中性，波动率适中

📈 **交易建议**
1. 短线：关注15分钟和1小时图表的突破信号
2. 中线：等待日线级别的明确趋势确认
3. 风控：单笔交易风险控制在2%以内

⚠️ **风险提示**
市场波动较大，建议采用分批建仓策略，避免单点进入。

您想了解特定币种的分析吗？`
    }
    
    if (input.includes('回测') || input.includes('优化')) {
      return `关于回测优化，我建议关注以下关键指标：

📊 **核心指标**
• 年化收益率：目标 > 15%
• 最大回撤：控制在 < 10%
• 夏普比率：优秀策略 > 1.5
• 胜率：平衡型策略通常 50-70%

🔧 **优化建议**
1. **参数调优**：使用网格搜索或贝叶斯优化
2. **样本外测试**：避免过拟合，保留30%数据做验证
3. **滑点成本**：加入真实的交易成本计算
4. **风险管理**：添加动态止损和仓位管理

需要我帮您分析具体的回测结果吗？`
    }
    
    // 默认回复
    const responses = [
      '这是一个很好的问题！让我来分析一下您的需求。',
      '基于我的分析，我建议您考虑以下几个方面...',
      '从技术角度来看，这个问题涉及多个因素。',
      '我理解您的关注点。让我为您提供一些专业建议。'
    ]
    
    return responses[Math.floor(Math.random() * responses.length)] + '\n\n如果您需要更具体的帮助，请提供更多详细信息，我会为您制定个性化的解决方案。'
  }
  
  // 处理快速问题
  const handleQuickQuestion = (question: string) => {
    setInputValue(question)
    inputRef.current?.focus()
  }
  
  // 处理键盘事件
  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault()
      handleSendMessage()
    }
  }
  
  // 预设快速问题
  const quickQuestions = [
    '帮我分析当前BTC走势',
    '生成一个网格交易策略',
    '如何优化策略的夏普比率？',
    '解释MACD指标的使用方法'
  ]

  return (
    <div className="min-h-screen bg-gray-50 p-4">
      {/* 页面容器 */}
      <div className="max-w-[1440px] mx-auto bg-white rounded-xl shadow-lg border border-gray-200 overflow-hidden">
        {/* Header */}
        <header className="py-4 px-8 flex justify-between border-b border-gray-200">
          {/* Logo */}
          <div className="flex items-center">
            <div className="w-7 h-7 rounded-full bg-brand-500 flex items-center justify-center mr-3">
              <span className="text-white font-bold text-sm">T</span>
            </div>
            <h1 className="text-xl font-bold text-brand-500">Trademe</h1>
            <span className="ml-3 px-3 py-1 bg-purple-100 text-purple-700 rounded-full text-sm font-medium">
              AI助手
            </span>
          </div>
          
          {/* Navigation */}
          <nav className="flex gap-2">
            <button onClick={() => navigate('/')} className="nav-item">
              首页
            </button>
            <button onClick={() => navigate('/strategies')} className="nav-item">
              策略交易
            </button>
            <button onClick={() => navigate('/trading')} className="nav-item">
              图表交易
            </button>
            <button className="nav-item active">
              AI助手
            </button>
            <button onClick={() => navigate('/insights')} className="nav-item">
              交易心得
            </button>
            <button onClick={() => navigate('/profile')} className="nav-item">
              账户中心
            </button>
          </nav>
          
          {/* User Profile */}
          <div className="flex items-center space-x-4">
            {/* 连接状态 */}
            <div className="flex items-center space-x-2 text-sm">
              <div className={`w-2 h-2 rounded-full ${
                isConnected ? 'bg-green-500' : 'bg-red-500'
              }`}></div>
              <span className="text-gray-600">
                {isConnected ? 'AI服务已连接' : 'AI服务断开'}
              </span>
            </div>
            
            <div className="w-9 h-9 rounded-full bg-gray-200 overflow-hidden cursor-pointer">
              {user?.avatar_url ? (
                <img 
                  src={user.avatar_url} 
                  alt="User avatar" 
                  className="w-full h-full object-cover"
                />
              ) : (
                <div className="w-full h-full flex items-center justify-center bg-brand-100 text-brand-600 font-medium">
                  {user?.username?.charAt(0).toUpperCase() || 'U'}
                </div>
              )}
            </div>
          </div>
        </header>

        {/* 主要内容 */}
        <main className="flex h-[calc(100vh-120px)]">
          {/* 聊天区域 */}
          <div className="flex-1 flex flex-col">
            {/* 消息列表 */}
            <div className="flex-1 overflow-y-auto p-6 space-y-4">
              {messages.map((message) => (
                <div
                  key={message.id}
                  className={`flex ${message.type === 'user' ? 'justify-end' : 'justify-start'}`}
                >
                  <div className={`max-w-[70%] ${message.type === 'user' ? 'order-2' : 'order-1'}`}>
                    <div
                      className={`rounded-2xl px-4 py-3 ${
                        message.type === 'user'
                          ? 'bg-brand-500 text-white'
                          : 'bg-gray-100 text-gray-900'
                      }`}
                    >
                      {message.isLoading ? (
                        <div className="flex items-center space-x-2">
                          <div className="flex space-x-1">
                            <div className="w-2 h-2 bg-gray-400 rounded-full animate-bounce"></div>
                            <div className="w-2 h-2 bg-gray-400 rounded-full animate-bounce" style={{ animationDelay: '0.1s' }}></div>
                            <div className="w-2 h-2 bg-gray-400 rounded-full animate-bounce" style={{ animationDelay: '0.2s' }}></div>
                          </div>
                          <span className="text-sm text-gray-500">AI正在思考...</span>
                        </div>
                      ) : (
                        <pre className="whitespace-pre-wrap font-sans text-sm leading-relaxed">
                          {message.content}
                        </pre>
                      )}
                    </div>
                    <div
                      className={`text-xs text-gray-500 mt-1 ${
                        message.type === 'user' ? 'text-right' : 'text-left'
                      }`}
                    >
                      {formatDateTime(message.timestamp, 'short')}
                    </div>
                  </div>
                  
                  {/* 头像 */}
                  <div className={`w-8 h-8 rounded-full flex-shrink-0 ${
                    message.type === 'user' ? 'order-1 ml-3' : 'order-2 mr-3'
                  }`}>
                    {message.type === 'user' ? (
                      <div className="w-full h-full bg-brand-100 text-brand-600 rounded-full flex items-center justify-center text-sm font-medium">
                        {user?.username?.charAt(0).toUpperCase() || 'U'}
                      </div>
                    ) : (
                      <div className="w-full h-full bg-purple-100 text-purple-600 rounded-full flex items-center justify-center">
                        <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9.663 17h4.673M12 3v1m6.364 1.636l-.707.707M21 12h-1M4 12H3m3.343-5.657l-.707-.707m2.828 9.9a5 5 0 117.072 0l-.548.547A3.374 3.374 0 0014 18.469V19a2 2 0 11-4 0v-.531c0-.895-.356-1.754-.988-2.386l-.548-.547z" />
                        </svg>
                      </div>
                    )}
                  </div>
                </div>
              ))}
              <div ref={messagesEndRef} />
            </div>
            
            {/* 快速问题 */}
            {messages.length <= 1 && (
              <div className="px-6 pb-4">
                <div className="flex flex-wrap gap-2">
                  {quickQuestions.map((question, index) => (
                    <button
                      key={index}
                      onClick={() => handleQuickQuestion(question)}
                      className="px-3 py-2 text-sm bg-gray-100 hover:bg-gray-200 rounded-lg transition-colors"
                    >
                      {question}
                    </button>
                  ))}
                </div>
              </div>
            )}
            
            {/* 输入区域 */}
            <div className="p-6 border-t border-gray-200">
              {!isPremium && (
                <div className="mb-4 p-3 bg-yellow-50 border border-yellow-200 rounded-lg">
                  <div className="flex items-center space-x-2 text-yellow-700">
                    <svg className="w-5 h-5" fill="currentColor" viewBox="0 0 20 20">
                      <path fillRule="evenodd" d="M8.257 3.099c.765-1.36 2.722-1.36 3.486 0l5.58 9.92c.75 1.334-.213 2.98-1.742 2.98H4.42c-1.53 0-2.493-1.646-1.743-2.98l5.58-9.92zM11 13a1 1 0 11-2 0 1 1 0 012 0zm-1-8a1 1 0 00-1 1v3a1 1 0 002 0V6a1 1 0 00-1-1z" clipRule="evenodd" />
                    </svg>
                    <span className="text-sm font-medium">AI对话功能需要高级版本，请升级账户后使用</span>
                  </div>
                </div>
              )}
              
              <div className="flex items-end space-x-4">
                <div className="flex-1">
                  <textarea
                    ref={inputRef}
                    value={inputValue}
                    onChange={(e) => setInputValue(e.target.value)}
                    onKeyPress={handleKeyPress}
                    placeholder={isPremium ? "请输入您的问题..." : "升级到高级版本后即可使用AI对话"}
                    disabled={!isPremium || isTyping}
                    className="w-full px-4 py-3 border border-gray-300 rounded-lg resize-none focus:ring-2 focus:ring-brand-500 focus:border-transparent disabled:bg-gray-100 disabled:cursor-not-allowed"
                    rows={2}
                    maxLength={1000}
                  />
                  <div className="flex justify-between items-center mt-2 text-xs text-gray-500">
                    <span>按Enter发送，Shift+Enter换行</span>
                    <span>{inputValue.length}/1000</span>
                  </div>
                </div>
                
                <button
                  onClick={handleSendMessage}
                  disabled={!inputValue.trim() || !isPremium || isTyping}
                  className="px-6 py-3 bg-brand-500 text-white rounded-lg font-medium hover:bg-brand-600 disabled:bg-gray-300 disabled:cursor-not-allowed transition-colors flex items-center space-x-2"
                >
                  {isTyping ? (
                    <>
                      <div className="w-4 h-4 border-2 border-white border-t-transparent rounded-full animate-spin"></div>
                      <span>发送中</span>
                    </>
                  ) : (
                    <>
                      <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 19l9 2-9-18-9 18 9-2zm0 0v-8" />
                      </svg>
                      <span>发送</span>
                    </>
                  )}
                </button>
              </div>
            </div>
          </div>
          
          {/* 侧边栏 */}
          <div className="w-80 border-l border-gray-200 bg-gray-50 p-6">
            <div className="space-y-6">
              {/* AI能力介绍 */}
              <div className="bg-white rounded-lg p-4 shadow-sm">
                <h3 className="font-semibold text-gray-900 mb-3">AI助手能力</h3>
                <div className="space-y-2 text-sm text-gray-600">
                  <div className="flex items-center space-x-2">
                    <svg className="w-4 h-4 text-green-500" fill="currentColor" viewBox="0 0 20 20">
                      <path fillRule="evenodd" d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z" clipRule="evenodd" />
                    </svg>
                    <span>市场趋势分析</span>
                  </div>
                  <div className="flex items-center space-x-2">
                    <svg className="w-4 h-4 text-green-500" fill="currentColor" viewBox="0 0 20 20">
                      <path fillRule="evenodd" d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z" clipRule="evenodd" />
                    </svg>
                    <span>策略代码生成</span>
                  </div>
                  <div className="flex items-center space-x-2">
                    <svg className="w-4 h-4 text-green-500" fill="currentColor" viewBox="0 0 20 20">
                      <path fillRule="evenodd" d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z" clipRule="evenodd" />
                    </svg>
                    <span>风险评估建议</span>
                  </div>
                  <div className="flex items-center space-x-2">
                    <svg className="w-4 h-4 text-green-500" fill="currentColor" viewBox="0 0 20 20">
                      <path fillRule="evenodd" d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z" clipRule="evenodd" />
                    </svg>
                    <span>回测结果解读</span>
                  </div>
                </div>
              </div>
              
              {/* 使用统计 */}
              <div className="bg-white rounded-lg p-4 shadow-sm">
                <h3 className="font-semibold text-gray-900 mb-3">使用统计</h3>
                <div className="space-y-3">
                  <div className="flex justify-between items-center">
                    <span className="text-sm text-gray-600">今日对话</span>
                    <span className="font-medium">{isPremium ? '12' : '0'} 次</span>
                  </div>
                  <div className="flex justify-between items-center">
                    <span className="text-sm text-gray-600">本月剩余</span>
                    <span className="font-medium">{isPremium ? '488' : '0'} 次</span>
                  </div>
                  <div className="w-full bg-gray-200 rounded-full h-2">
                    <div 
                      className="bg-brand-500 h-2 rounded-full" 
                      style={{ width: isPremium ? '12%' : '0%' }}
                    ></div>
                  </div>
                </div>
              </div>
              
              {/* 最近话题 */}
              <div className="bg-white rounded-lg p-4 shadow-sm">
                <h3 className="font-semibold text-gray-900 mb-3">最近话题</h3>
                <div className="space-y-2 text-sm">
                  <button className="w-full text-left p-2 rounded hover:bg-gray-50 transition-colors">
                    <div className="font-medium text-gray-900">BTC趋势分析</div>
                    <div className="text-gray-500">2小时前</div>
                  </button>
                  <button className="w-full text-left p-2 rounded hover:bg-gray-50 transition-colors">
                    <div className="font-medium text-gray-900">网格策略优化</div>
                    <div className="text-gray-500">昨天</div>
                  </button>
                  <button className="w-full text-left p-2 rounded hover:bg-gray-50 transition-colors">
                    <div className="font-medium text-gray-900">风险管理建议</div>
                    <div className="text-gray-500">3天前</div>
                  </button>
                </div>
              </div>
            </div>
          </div>
        </main>
      </div>
    </div>
  )
}

export default AIChatPage