import { tradingServiceClient, handleApiResponse, handleApiError } from './client'

export interface ChatMessage {
  role: 'user' | 'assistant'
  content: string
  timestamp: string
}

export interface ChatSession {
  id: string
  title: string
  messages: ChatMessage[]
  created_at: string
  updated_at: string
}

export interface StrategyGenerationRequest {
  description: string
  market_type: string
  risk_level: 'low' | 'medium' | 'high'
  timeframe: string
  indicators?: string[]
}

export interface GeneratedStrategy {
  name: string
  description: string
  code: string
  explanation: string
  parameters: Record<string, any>
  risk_assessment: {
    level: 'low' | 'medium' | 'high'
    factors: string[]
    recommendations: string[]
  }
}

export interface MarketAnalysis {
  symbol: string
  timeframe: string
  trend_direction: 'bullish' | 'bearish' | 'neutral'
  strength: number
  support_levels: number[]
  resistance_levels: number[]
  technical_indicators: Record<string, any>
  summary: string
  recommendations: string[]
  confidence: number
}

export const aiApi = {
  // 创建聊天会话
  async createChatSession(title?: string): Promise<ChatSession> {
    try {
      const response = await tradingServiceClient.post('/ai/chat/sessions', { title })
      return handleApiResponse(response)
    } catch (error) {
      handleApiError(error)
    }
  },

  // 获取聊天会话列表
  async getChatSessions(): Promise<ChatSession[]> {
    try {
      const response = await tradingServiceClient.get('/ai/chat/sessions')
      return handleApiResponse(response)
    } catch (error) {
      handleApiError(error)
    }
  },

  // 获取单个聊天会话
  async getChatSession(sessionId: string): Promise<ChatSession> {
    try {
      const response = await tradingServiceClient.get(`/ai/chat/sessions/${sessionId}`)
      return handleApiResponse(response)
    } catch (error) {
      handleApiError(error)
    }
  },

  // 发送聊天消息
  async sendMessage(sessionId: string, message: string): Promise<ChatMessage> {
    try {
      const response = await tradingServiceClient.post(`/ai/chat/sessions/${sessionId}/messages`, {
        content: message
      })
      return handleApiResponse(response)
    } catch (error) {
      handleApiError(error)
    }
  },

  // 删除聊天会话
  async deleteChatSession(sessionId: string): Promise<{ message: string }> {
    try {
      const response = await tradingServiceClient.delete(`/ai/chat/sessions/${sessionId}`)
      return handleApiResponse(response)
    } catch (error) {
      handleApiError(error)
    }
  },

  // 生成交易策略
  async generateStrategy(request: StrategyGenerationRequest): Promise<GeneratedStrategy> {
    try {
      const response = await tradingServiceClient.post('/ai/generate/strategy', request)
      return handleApiResponse(response)
    } catch (error) {
      handleApiError(error)
    }
  },

  // 优化策略代码
  async optimizeStrategy(code: string, objectives: string[]): Promise<{
    optimized_code: string
    improvements: string[]
    performance_prediction: Record<string, number>
  }> {
    try {
      const response = await tradingServiceClient.post('/ai/optimize/strategy', {
        code,
        objectives
      })
      return handleApiResponse(response)
    } catch (error) {
      handleApiError(error)
    }
  },

  // 分析市场数据
  async analyzeMarket(
    symbol: string,
    exchange: string,
    timeframe: string,
    lookback_days: number = 30
  ): Promise<MarketAnalysis> {
    try {
      const response = await tradingServiceClient.post('/ai/analyze/market', {
        symbol,
        exchange,
        timeframe,
        lookback_days
      })
      return handleApiResponse(response)
    } catch (error) {
      handleApiError(error)
    }
  },

  // 解释策略代码
  async explainStrategy(code: string): Promise<{
    explanation: string
    logic_breakdown: string[]
    risk_factors: string[]
    usage_tips: string[]
  }> {
    try {
      const response = await tradingServiceClient.post('/ai/explain/strategy', { code })
      return handleApiResponse(response)
    } catch (error) {
      handleApiError(error)
    }
  },

  // 分析回测结果
  async analyzeBacktestResults(backtestId: string): Promise<{
    performance_analysis: string
    strengths: string[]
    weaknesses: string[]
    improvement_suggestions: string[]
    risk_assessment: string
    market_conditions_impact: string
  }> {
    try {
      const response = await tradingServiceClient.post(`/ai/analyze/backtest/${backtestId}`)
      return handleApiResponse(response)
    } catch (error) {
      handleApiError(error)
    }
  },

  // 获取交易信号
  async getTradingSignals(
    symbol: string,
    exchange: string,
    timeframes: string[]
  ): Promise<{
    signals: Array<{
      timeframe: string
      signal: 'buy' | 'sell' | 'hold'
      confidence: number
      reason: string
      entry_price?: number
      stop_loss?: number
      take_profit?: number
    }>
    overall_sentiment: 'bullish' | 'bearish' | 'neutral'
    market_conditions: string
  }> {
    try {
      const response = await tradingServiceClient.post('/ai/signals', {
        symbol,
        exchange,
        timeframes
      })
      return handleApiResponse(response)
    } catch (error) {
      handleApiError(error)
    }
  },

  // 风险评估
  async assessRisk(
    strategy_code?: string,
    portfolio?: Array<{ symbol: string; allocation: number }>
  ): Promise<{
    risk_level: 'low' | 'medium' | 'high'
    risk_score: number
    risk_factors: string[]
    recommendations: string[]
    portfolio_correlation?: number
    expected_volatility?: number
  }> {
    try {
      const response = await tradingServiceClient.post('/ai/assess/risk', {
        strategy_code,
        portfolio
      })
      return handleApiResponse(response)
    } catch (error) {
      handleApiError(error)
    }
  }
}