import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom'
import { useUserInfo } from '../store'
import toast from 'react-hot-toast'

// API密钥接口定义
interface APIKey {
  id: string
  name: string
  exchange: string
  apiKey: string
  permissions: string[]
  status: 'active' | 'inactive' | 'error'
  createdAt: string
  lastUsed?: string
}

const APIManagementPage: React.FC = () => {
  const navigate = useNavigate()
  const { user, isPremium } = useUserInfo()

  // 表单状态
  const [showAddForm, setShowAddForm] = useState(false)
  const [formData, setFormData] = useState({
    name: '',
    exchange: 'binance',
    apiKey: '',
    secretKey: '',
    passphrase: '', // 部分交易所需要
    permissions: ['read'] as string[]
  })

  // 模拟API密钥数据 - 基于原型
  const [apiKeys, setApiKeys] = useState<APIKey[]>([
    {
      id: '1',
      name: '币安主账户',
      exchange: 'binance',
      apiKey: 'bN1A...k2D9',
      permissions: ['read', 'spot_trade'],
      status: 'active',
      createdAt: '2024-05-10',
      lastUsed: '2小时前'
    },
    {
      id: '2',
      name: 'OKX交易账户',
      exchange: 'okx',
      apiKey: 'okx4...7h3f',
      permissions: ['read', 'spot_trade'],
      status: 'active',
      createdAt: '2024-05-12',
      lastUsed: '1天前'
    },
    {
      id: '3',
      name: '测试密钥',
      exchange: 'huobi',
      apiKey: 'hub2...9x1q',
      permissions: ['read'],
      status: 'inactive',
      createdAt: '2024-05-08'
    }
  ])

  // 交易所配置
  const exchanges = [
    { value: 'binance', label: '币安 (Binance)', needsPassphrase: false },
    { value: 'okx', label: 'OKX', needsPassphrase: true },
    { value: 'huobi', label: '火币 (Huobi)', needsPassphrase: false },
    { value: 'coinbase', label: 'Coinbase Pro', needsPassphrase: true },
    { value: 'kraken', label: 'Kraken', needsPassphrase: false }
  ]

  // 权限选项
  const permissionOptions = [
    { value: 'read', label: '只读权限', description: '查看账户余额和交易记录' },
    { value: 'spot_trade', label: '现货交易', description: '执行现货买卖交易' },
    { value: 'futures_trade', label: '合约交易', description: '执行期货合约交易' },
    { value: 'withdraw', label: '提现权限', description: '资金提取功能' }
  ]

  // 处理表单提交
  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!formData.name || !formData.apiKey || !formData.secretKey) {
      toast.error('请填写完整的API信息')
      return
    }

    if (!isPremium && apiKeys.length >= 2) {
      toast.error('基础版本最多添加2个API密钥，请升级到高级版本')
      return
    }

    const newApiKey: APIKey = {
      id: Date.now().toString(),
      name: formData.name,
      exchange: formData.exchange,
      apiKey: formData.apiKey.substring(0, 4) + '...' + formData.apiKey.slice(-4),
      permissions: formData.permissions,
      status: 'active',
      createdAt: new Date().toISOString().split('T')[0]
    }

    setApiKeys([...apiKeys, newApiKey])
    setShowAddForm(false)
    setFormData({
      name: '',
      exchange: 'binance',
      apiKey: '',
      secretKey: '',
      passphrase: '',
      permissions: ['read']
    })
    toast.success('API密钥添加成功')
  }

  // 删除API密钥
  const handleDelete = (id: string) => {
    if (confirm('确定要删除此API密钥吗？')) {
      setApiKeys(apiKeys.filter(key => key.id !== id))
      toast.success('API密钥已删除')
    }
  }

  // 测试连接
  const handleTest = (id: string) => {
    toast.success('连接测试通过')
  }

  // 获取交易所标签
  const getExchangeLabel = (exchange: string) => {
    return exchanges.find(ex => ex.value === exchange)?.label || exchange
  }

  // 获取状态样式
  const getStatusBadge = (status: string) => {
    const styles = {
      active: 'bg-green-50 text-green-700 border-green-200',
      inactive: 'bg-gray-50 text-gray-700 border-gray-200',
      error: 'bg-red-50 text-red-700 border-red-200'
    }
    const labels = {
      active: '正常',
      inactive: '未激活',
      error: '错误'
    }
    return (
      <span className={`inline-flex items-center px-2 py-1 rounded-md text-xs font-medium border ${styles[status as keyof typeof styles]}`}>
        {labels[status as keyof typeof labels]}
      </span>
    )
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* 页面容器 - 基于原型设计 */}
      <div className="w-full max-w-7xl min-h-screen bg-white rounded-xl shadow-xl border border-gray-200 mx-auto">
        
        {/* 头部导航 */}
        <header className="py-4 px-8 flex justify-between border-b border-gray-200">
          <div className="flex items-center">
            <div className="w-7 h-7 mr-3 flex items-center justify-center">
              <svg viewBox="0 0 24 24" className="w-full h-full" style={{color: '#1a3d7c'}}>
                <circle cx="12" cy="12" r="10" fill="none" stroke="currentColor" strokeWidth="2"/>
                <path d="M8 12h8M12 8v8" stroke="currentColor" strokeWidth="2"/>
              </svg>
            </div>
            <h1 className="text-xl font-bold" style={{ color: '#1a3d7c' }}>Trademe</h1>
          </div>
          
          <nav className="flex gap-2">
            <button onClick={() => navigate('/')} className="px-4 py-2 text-blue-600 font-medium hover:bg-blue-50 rounded-lg">首页</button>
            <button onClick={() => navigate('/strategies')} className="px-4 py-2 text-blue-600 font-medium hover:bg-blue-50 rounded-lg">策略交易</button>
            <button onClick={() => navigate('/trading')} className="px-4 py-2 text-blue-600 font-medium hover:bg-blue-50 rounded-lg">图表交易</button>
            <button className="px-4 py-2 bg-blue-600 text-white font-medium rounded-lg">API管理</button>
            <button className="px-4 py-2 text-blue-600 font-medium hover:bg-blue-50 rounded-lg">交易心得</button>
            <button onClick={() => navigate('/profile')} className="px-4 py-2 text-blue-600 font-medium hover:bg-blue-50 rounded-lg">账户中心</button>
          </nav>
          
          <div className="flex items-center">
            <div className="w-9 h-9 rounded-full bg-gray-200 overflow-hidden">
              {user?.avatar_url ? (
                <img src={user.avatar_url} alt="User avatar" className="w-full h-full object-cover" />
              ) : (
                <div className="w-full h-full flex items-center justify-center bg-blue-100 text-blue-600 font-medium">
                  {user?.username?.charAt(0).toUpperCase() || 'U'}
                </div>
              )}
            </div>
          </div>
        </header>

        <div className="p-8">
          {/* 页面标题和描述 - 基于原型 */}
          <div className="mb-8">
            <h2 className="text-2xl font-bold mb-2" style={{ color: '#1a3d7c' }}>API密钥管理</h2>
            <p className="text-gray-600 mb-4">安全管理您的交易所API密钥，用于自动化交易和数据获取</p>
            
            {/* 安全提示 */}
            <div className="bg-yellow-50 border border-yellow-200 rounded-lg p-4 mb-6">
              <div className="flex">
                <svg className="w-5 h-5 text-yellow-600 mr-3 mt-0.5" fill="currentColor" viewBox="0 0 20 20">
                  <path fillRule="evenodd" d="M8.257 3.099c.765-1.36 2.722-1.36 3.486 0l5.58 9.92c.75 1.334-.213 2.98-1.742 2.98H4.42c-1.53 0-2.493-1.646-1.743-2.98l5.58-9.92zM11 13a1 1 0 11-2 0 1 1 0 012 0zm-1-8a1 1 0 00-1 1v3a1 1 0 002 0V6a1 1 0 00-1-1z" clipRule="evenodd" />
                </svg>
                <div>
                  <h4 className="text-yellow-800 font-medium mb-2">🔐 API安全提示</h4>
                  <ul className="text-yellow-700 text-sm space-y-1">
                    <li>• 请确保API密钥来自官方交易所，切勿使用第三方生成的密钥</li>
                    <li>• 建议仅开启必要的权限，避免开启提现权限</li>
                    <li>• 定期检查API密钥的使用记录，如发现异常请立即删除</li>
                    <li>• 密钥信息经过加密存储，平台无法查看您的完整密钥</li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
          
          {/* 新增API密钥按钮 */}
          <div className="flex justify-between items-center mb-6">
            <div className="flex items-center space-x-4">
              <h3 className="text-xl font-bold">API密钥列表</h3>
              <span className="text-sm text-gray-500">
                ({apiKeys.length}/{isPremium ? '无限制' : '2'})
              </span>
            </div>
            <button
              onClick={() => setShowAddForm(!showAddForm)}
              className="bg-blue-600 text-white rounded-lg px-4 py-2 font-medium hover:bg-blue-700 transition-colors flex items-center"
            >
              <svg className="w-4 h-4 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 6v6m0 0v6m0-6h6m-6 0H6" />
              </svg>
              添加API密钥
            </button>
          </div>
          
          {/* 添加API密钥表单 */}
          {showAddForm && (
            <div className="bg-white border border-gray-200 rounded-xl p-6 mb-6">
              <h3 className="text-lg font-bold mb-4" style={{ color: '#1a3d7c' }}>新增API密钥</h3>
              <form onSubmit={handleSubmit}>
                <div className="grid grid-cols-2 gap-6">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">密钥名称</label>
                    <input
                      type="text"
                      value={formData.name}
                      onChange={(e) => setFormData({...formData, name: e.target.value})}
                      className="w-full border border-gray-200 bg-gray-50 rounded-lg px-4 py-3 text-sm focus:border-blue-600 focus:outline-none focus:bg-white"
                      placeholder="如：币安主账户"
                      required
                    />
                  </div>
                  
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">交易所</label>
                    <select
                      value={formData.exchange}
                      onChange={(e) => setFormData({...formData, exchange: e.target.value})}
                      className="w-full border border-gray-200 bg-gray-50 rounded-lg px-4 py-3 text-sm focus:border-blue-600 focus:outline-none focus:bg-white"
                    >
                      {exchanges.map(exchange => (
                        <option key={exchange.value} value={exchange.value}>{exchange.label}</option>
                      ))}
                    </select>
                  </div>
                  
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">API Key</label>
                    <input
                      type="password"
                      value={formData.apiKey}
                      onChange={(e) => setFormData({...formData, apiKey: e.target.value})}
                      className="w-full border border-gray-200 bg-gray-50 rounded-lg px-4 py-3 text-sm focus:border-blue-600 focus:outline-none focus:bg-white"
                      placeholder="输入API Key"
                      required
                    />
                  </div>
                  
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">Secret Key</label>
                    <input
                      type="password"
                      value={formData.secretKey}
                      onChange={(e) => setFormData({...formData, secretKey: e.target.value})}
                      className="w-full border border-gray-200 bg-gray-50 rounded-lg px-4 py-3 text-sm focus:border-blue-600 focus:outline-none focus:bg-white"
                      placeholder="输入Secret Key"
                      required
                    />
                  </div>
                  
                  {exchanges.find(ex => ex.value === formData.exchange)?.needsPassphrase && (
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-2">Passphrase</label>
                      <input
                        type="password"
                        value={formData.passphrase}
                        onChange={(e) => setFormData({...formData, passphrase: e.target.value})}
                        className="w-full border border-gray-200 bg-gray-50 rounded-lg px-4 py-3 text-sm focus:border-blue-600 focus:outline-none focus:bg-white"
                        placeholder="输入Passphrase（部分交易所需要）"
                      />
                    </div>
                  )}
                </div>
                
                {/* 权限设置 */}
                <div className="mt-6">
                  <label className="block text-sm font-medium text-gray-700 mb-3">权限设置</label>
                  <div className="grid grid-cols-2 gap-4">
                    {permissionOptions.map(option => (
                      <label key={option.value} className="flex items-start space-x-3 p-3 border border-gray-200 rounded-lg hover:bg-gray-50 cursor-pointer">
                        <input
                          type="checkbox"
                          checked={formData.permissions.includes(option.value)}
                          onChange={(e) => {
                            if (e.target.checked) {
                              setFormData({...formData, permissions: [...formData.permissions, option.value]})
                            } else {
                              setFormData({...formData, permissions: formData.permissions.filter(p => p !== option.value)})
                            }
                          }}
                          className="mt-0.5"
                        />
                        <div>
                          <div className="text-sm font-medium text-gray-900">{option.label}</div>
                          <div className="text-xs text-gray-500">{option.description}</div>
                        </div>
                      </label>
                    ))}
                  </div>
                </div>
                
                <div className="flex justify-end space-x-3 mt-6 pt-4 border-t border-gray-200">
                  <button
                    type="button"
                    onClick={() => setShowAddForm(false)}
                    className="px-4 py-2 text-gray-700 border border-gray-300 rounded-lg hover:bg-gray-50"
                  >
                    取消
                  </button>
                  <button
                    type="submit"
                    className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700"
                  >
                    添加密钥
                  </button>
                </div>
              </form>
            </div>
          )}
          
          {/* API密钥列表表格 */}
          <div className="bg-white border border-gray-200 rounded-xl overflow-hidden">
            <div className="overflow-x-auto">
              <table className="w-full">
                <thead>
                  <tr>
                    <th className="bg-gray-50 border-b border-gray-200 px-6 py-4 text-left text-xs font-semibold text-gray-500 uppercase tracking-wider">交易所</th>
                    <th className="bg-gray-50 border-b border-gray-200 px-6 py-4 text-left text-xs font-semibold text-gray-500 uppercase tracking-wider">密钥名称</th>
                    <th className="bg-gray-50 border-b border-gray-200 px-6 py-4 text-left text-xs font-semibold text-gray-500 uppercase tracking-wider">API Key</th>
                    <th className="bg-gray-50 border-b border-gray-200 px-6 py-4 text-left text-xs font-semibold text-gray-500 uppercase tracking-wider">权限</th>
                    <th className="bg-gray-50 border-b border-gray-200 px-6 py-4 text-left text-xs font-semibold text-gray-500 uppercase tracking-wider">状态</th>
                    <th className="bg-gray-50 border-b border-gray-200 px-6 py-4 text-left text-xs font-semibold text-gray-500 uppercase tracking-wider">创建时间</th>
                    <th className="bg-gray-50 border-b border-gray-200 px-6 py-4 text-left text-xs font-semibold text-gray-500 uppercase tracking-wider">操作</th>
                  </tr>
                </thead>
                <tbody>
                  {apiKeys.map((apiKey, index) => (
                    <tr key={apiKey.id} className={`${index % 2 === 0 ? 'bg-white' : 'bg-gray-50'} hover:bg-blue-50`}>
                      <td className="px-6 py-4 text-sm font-medium text-gray-900">
                        {getExchangeLabel(apiKey.exchange)}
                      </td>
                      <td className="px-6 py-4 text-sm text-gray-600">{apiKey.name}</td>
                      <td className="px-6 py-4 text-sm text-gray-600 font-mono">{apiKey.apiKey}</td>
                      <td className="px-6 py-4 text-sm text-gray-600">
                        <div className="flex flex-wrap gap-1">
                          {apiKey.permissions.map(permission => (
                            <span key={permission} className="px-2 py-1 bg-blue-100 text-blue-700 rounded text-xs">
                              {permissionOptions.find(p => p.value === permission)?.label || permission}
                            </span>
                          ))}
                        </div>
                      </td>
                      <td className="px-6 py-4 text-sm">{getStatusBadge(apiKey.status)}</td>
                      <td className="px-6 py-4 text-sm text-gray-600">
                        <div>{apiKey.createdAt}</div>
                        {apiKey.lastUsed && (
                          <div className="text-xs text-gray-500">最后使用: {apiKey.lastUsed}</div>
                        )}
                      </td>
                      <td className="px-6 py-4 text-sm">
                        <div className="flex space-x-2">
                          <button
                            onClick={() => handleTest(apiKey.id)}
                            className="text-blue-600 hover:text-blue-800 text-xs"
                            title="测试连接"
                          >
                            <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 10V3L4 14h7v7l9-11h-7z" />
                            </svg>
                          </button>
                          <button
                            className="text-gray-600 hover:text-gray-800 text-xs"
                            title="编辑"
                          >
                            <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M11 5H6a2 2 0 00-2 2v11a2 2 0 002 2h11a2 2 0 002-2v-5m-1.414-9.414a2 2 0 112.828 2.828L11.828 15H9v-2.828l8.586-8.586z" />
                            </svg>
                          </button>
                          <button
                            onClick={() => handleDelete(apiKey.id)}
                            className="text-red-600 hover:text-red-800 text-xs"
                            title="删除"
                          >
                            <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16" />
                            </svg>
                          </button>
                        </div>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>
          
          {/* API使用指南 */}
          <div className="mt-8 bg-gray-50 border border-gray-200 rounded-xl p-6">
            <h3 className="text-lg font-bold mb-4" style={{ color: '#1a3d7c' }}>📖 API使用指南</h3>
            <div className="grid grid-cols-2 gap-6 text-sm text-gray-700">
              <div>
                <h4 className="font-medium mb-2">权限说明</h4>
                <ul className="space-y-1">
                  <li><strong>只读权限</strong>：查看账户余额、历史订单等基础信息</li>
                  <li><strong>现货交易</strong>：执行现货买卖交易，不包含提现功能</li>
                  <li><strong>合约交易</strong>：执行期货合约交易（高风险，请谨慎开启）</li>
                  <li><strong>提现权限</strong>：资金提取功能（不建议开启）</li>
                </ul>
              </div>
              <div>
                <h4 className="font-medium mb-2">安全建议</h4>
                <ul className="space-y-1">
                  <li>• 使用专门的API子账户，避免使用主账户密钥</li>
                  <li>• 设置IP白名单，限制API密钥的访问来源</li>
                  <li>• 定期检查交易记录，确保没有异常交易</li>
                  <li>• 如发现账户异常，请立即禁用相关API密钥</li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default APIManagementPage