import React, { useState, useEffect, useRef } from 'react'
import { useNavigate } from 'react-router-dom'
import { 
  useUserInfo, 
  useWebSocketStatus, 
  useGlobalLoading
} from '../store'
import {
  useTradingForm,
  useTradingOrders,
  useTradingPositions,
  useTradingAccounts
} from '../store/tradingStore'
import useRealTimeData from '../hooks/useRealTimeData'
import * as echarts from 'echarts'
import toast from 'react-hot-toast'

const TradingPage: React.FC = () => {
  const navigate = useNavigate()
  const { user, isPremium } = useUserInfo()
  const { isConnected } = useWebSocketStatus()
  const { isLoading, isPlacingOrder } = useGlobalLoading()
  
  // Trading store hooks
  const { 
    tradingForm, 
    updateTradingForm, 
    resetTradingForm, 
    validateOrder,
    availableSymbols,
    loadAvailableSymbols 
  } = useTradingForm()
  
  const { 
    createOrder,
    activeOrders,
    orderHistory,
    loadOrders,
    isPlacingOrder: placingOrder
  } = useTradingOrders()
  
  const { 
    positions,
    totalPortfolioValue,
    totalUnrealizedPnL,
    loadPositions 
  } = useTradingPositions()
  
  const { 
    accounts,
    selectedExchange,
    supportedExchanges,
    switchExchange,
    loadAccountBalance,
    loadSupportedExchanges 
  } = useTradingAccounts()
  
  // UI状态 (保留为本地状态)
  const [selectedMarket, setSelectedMarket] = useState('BTC/USDT')
  
  // 实时数据连接
  const { 
    isConnected: realTimeConnected, 
    subscribeToSymbol, 
    unsubscribeFromSymbol 
  } = useRealTimeData({
    enableMarketData: true,
    enableTradingUpdates: isPremium,
    enableOrderUpdates: isPremium,
    enablePositionUpdates: isPremium,
    symbols: [selectedMarket]
  })
  
  const chartRef = useRef<HTMLDivElement>(null)
  const chartInstance = useRef<echarts.ECharts | null>(null)
  const [selectedTimeframe, setSelectedTimeframe] = useState('15m')
  const [currentPrice, setCurrentPrice] = useState('43,250.00')
  const [priceChange, setPriceChange] = useState('+2.34%')
  const [priceChangePercent, setPriceChangePercent] = useState(2.34)
  
  // 绘图工具状态
  const [activeDrawingTool, setActiveDrawingTool] = useState<string | null>(null)
  const [showAIAssistant, setShowAIAssistant] = useState(false)
  const [aiMessage, setAiMessage] = useState('')

  // 初始化交易数据
  useEffect(() => {
    const initializeTradingData = async () => {
      if (isPremium) {
        await Promise.allSettled([
          loadSupportedExchanges(),
          loadAccountBalance(selectedExchange),
          loadAvailableSymbols(selectedExchange),
          loadOrders(),
          loadPositions()
        ])
      }
    }

    initializeTradingData()
  }, [isPremium, selectedExchange])

  // 监听交易所变化
  useEffect(() => {
    if (selectedExchange && availableSymbols.length === 0) {
      loadAvailableSymbols(selectedExchange)
    }
  }, [selectedExchange, availableSymbols.length])

  // 初始化ECharts图表
  useEffect(() => {
    if (!chartRef.current) return
    
    chartInstance.current = echarts.init(chartRef.current)
    
    // 模拟K线数据
    const klineData = generateMockKlineData()
    
    const option: echarts.EChartsOption = {
      backgroundColor: '#ffffff',
      animation: true,
      grid: {
        top: '10%',
        left: '8%',
        right: '8%',
        bottom: '15%'
      },
      xAxis: {
        type: 'category',
        data: klineData.map(item => item[0]),
        boundaryGap: false,
        axisLine: { lineStyle: { color: '#8392A5' } },
        axisLabel: { color: '#8392A5' }
      },
      yAxis: {
        scale: true,
        axisLine: { lineStyle: { color: '#8392A5' } },
        axisLabel: { color: '#8392A5' },
        splitLine: {
          show: true,
          lineStyle: { color: '#f0f0f0' }
        }
      },
      tooltip: {
        trigger: 'axis',
        axisPointer: { type: 'cross' },
        backgroundColor: 'rgba(245, 245, 245, 0.9)',
        borderWidth: 1,
        borderColor: '#ccc',
        textStyle: { color: '#000' },
        formatter: function(params: any) {
          const data = params[0].data
          return `
            <div style="padding: 10px;">
              <div style="font-weight: bold; margin-bottom: 5px;">${selectedMarket} - ${data[0]}</div>
              <div>开盘: ${data[1]}</div>
              <div>收盘: ${data[2]}</div>
              <div>最低: ${data[3]}</div>
              <div>最高: ${data[4]}</div>
              <div>成交量: ${data[5]}</div>
            </div>
          `
        }
      },
      series: [
        {
          name: 'K线',
          type: 'candlestick',
          data: klineData.map(item => [item[1], item[2], item[3], item[4]]),
          itemStyle: {
            color: '#00da3c',
            color0: '#ec0000',
            borderColor: '#00da3c',
            borderColor0: '#ec0000'
          }
        }
      ]
    }
    
    chartInstance.current.setOption(option)
    
    // 处理窗口大小变化
    const handleResize = () => {
      chartInstance.current?.resize()
    }
    
    window.addEventListener('resize', handleResize)
    
    return () => {
      window.removeEventListener('resize', handleResize)
      chartInstance.current?.dispose()
    }
  }, [selectedMarket, selectedTimeframe])
  
  // 生成模拟K线数据
  const generateMockKlineData = () => {
    const data = []
    let basePrice = 43250
    const now = new Date()
    
    for (let i = 100; i >= 0; i--) {
      const date = new Date(now.getTime() - i * 15 * 60 * 1000)
      const timeStr = `${date.getHours().toString().padStart(2, '0')}:${date.getMinutes().toString().padStart(2, '0')}`
      
      const open = basePrice + (Math.random() - 0.5) * 100
      const close = open + (Math.random() - 0.5) * 200
      const high = Math.max(open, close) + Math.random() * 50
      const low = Math.min(open, close) - Math.random() * 50
      const volume = Math.random() * 100 + 50
      
      data.push([timeStr, open.toFixed(2), close.toFixed(2), low.toFixed(2), high.toFixed(2), volume.toFixed(2)])
      basePrice = close
    }
    
    return data
  }
  
  // 处理交易参数变化 - 现在使用tradingStore
  const handlePriceChange = (value: string) => {
    updateTradingForm('price', value)
  }
  
  const handleQuantityChange = (value: string) => {
    updateTradingForm('quantity', value)
  }
  
  const handleAmountChange = (value: string) => {
    updateTradingForm('amount', value)
  }

  // 处理交易类型变化
  const handleTradeTypeChange = (type: 'buy' | 'sell') => {
    updateTradingForm('side', type)
  }

  // 处理订单类型变化
  const handleOrderTypeChange = (type: 'limit' | 'market') => {
    updateTradingForm('order_type', type)
  }

  // 处理交易所切换
  const handleExchangeSwitch = (exchange: string) => {
    switchExchange(exchange)
    updateTradingForm('exchange', exchange)
  }

  // 处理交易对变化
  const handleSymbolChange = (symbol: string) => {
    // 取消旧的订阅
    unsubscribeFromSymbol(selectedMarket)
    
    // 更新选择的交易对
    setSelectedMarket(symbol)
    updateTradingForm('symbol', symbol)
    
    // 订阅新的交易对
    if (realTimeConnected) {
      subscribeToSymbol(symbol)
    }
  }
  
  // 处理下单
  const handlePlaceOrder = async () => {
    if (!isPremium) {
      toast.error('实盘交易需要升级到高级版本')
      return
    }
    
    if (!tradingForm.quantity || (tradingForm.order_type === 'limit' && !tradingForm.price)) {
      toast.error('请填写完整的交易信息')
      return
    }
    
    try {
      // 先验证订单
      const riskAssessment = await validateOrder()
      if (riskAssessment && !riskAssessment.approved) {
        toast.error(`订单被风控拒绝: ${riskAssessment.violations.join(', ')}`)
        return
      }

      // 创建订单
      const orderData = {
        exchange: tradingForm.exchange,
        symbol: tradingForm.symbol,
        order_type: tradingForm.order_type,
        side: tradingForm.side,
        amount: parseFloat(tradingForm.quantity),
        price: tradingForm.order_type === 'limit' ? parseFloat(tradingForm.price) : undefined
      }

      const order = await createOrder(orderData)
      if (order) {
        toast.success(`${tradingForm.side === 'buy' ? '买入' : '卖出'}订单已提交`)
        resetTradingForm()
      }
    } catch (error) {
      toast.error(`下单失败: ${error instanceof Error ? error.message : '未知错误'}`)
    }
  }

  return (
    <>
      {/* 市场信息栏 */}
      <div className="px-6 py-4 border-b border-gray-200 bg-gray-50 -mx-6 -mt-6 mb-6">
          <div className="flex items-center justify-between">
            {/* 连接状态 */}
            <div className="flex items-center space-x-2 text-sm">
              <div className={`w-2 h-2 rounded-full ${
                realTimeConnected ? 'bg-green-500' : 'bg-red-500'
              }`}></div>
              <span className="text-gray-600">
                {realTimeConnected ? '✅ 实时数据已连接' : '❌ 数据连接断开'}
              </span>
            </div>
            
            <div className="flex items-center space-x-6">
              <div className="flex items-center space-x-2">
                <span className="font-bold text-lg">{selectedMarket}</span>
                <select 
                  value={selectedMarket}
                  onChange={(e) => handleSymbolChange(e.target.value)}
                  className="text-sm border-none bg-transparent focus:outline-none cursor-pointer"
                >
                  {availableSymbols.length > 0 ? (
                    availableSymbols.map(symbol => (
                      <option key={symbol} value={symbol}>{symbol}</option>
                    ))
                  ) : (
                    <>
                      <option value="BTC/USDT">BTC/USDT</option>
                      <option value="ETH/USDT">ETH/USDT</option>
                      <option value="BNB/USDT">BNB/USDT</option>
                      <option value="SOL/USDT">SOL/USDT</option>
                      <option value="ADA/USDT">ADA/USDT</option>
                    </>
                  )}
                </select>
              </div>
              <div className="flex items-center space-x-4">
                <div>
                  <span className="text-2xl font-bold">{currentPrice}</span>
                  <span className={`ml-2 font-semibold ${
                    priceChangePercent >= 0 ? 'text-green-600' : 'text-red-600'
                  }`}>{priceChange}</span>
                </div>
                <div className="text-sm text-gray-500">
                  <div>24H最高: 44,100.00</div>
                  <div>24H最低: 42,800.00</div>
                </div>
                <div className="text-sm text-gray-500">
                  <div>24H成交量: 1,234.56 BTC</div>
                  <div>24H成交额: 53.2M USDT</div>
                </div>
              </div>
            </div>
            <div className="flex items-center space-x-2">
              {['1m', '5m', '15m', '1h', '4h', '1d'].map((tf) => (
                <button 
                  key={tf}
                  onClick={() => setSelectedTimeframe(tf)}
                  className={`px-3 py-1 rounded text-sm transition-colors ${
                    selectedTimeframe === tf
                      ? 'bg-brand-500 text-white' 
                      : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                  }`}
                >
                  {tf === '1m' ? '1分钟' : tf === '5m' ? '5分钟' : tf === '15m' ? '15分钟' : 
                   tf === '1h' ? '1小时' : tf === '4h' ? '4小时' : '1天'}
                </button>
              ))}
            </div>
          </div>
        </div>

        {/* 主要交易界面 */}
        <div className="p-6">
          <div className="grid grid-cols-12 gap-6 h-[calc(100vh-280px)]">
            {/* 左侧：K线图表 */}
            <div className="col-span-8">
              <div className="h-full bg-white border border-gray-100 rounded-xl shadow-sm overflow-hidden">
                {/* 图表工具栏 */}
                <div className="p-3 border-b border-gray-100 bg-gray-50 flex items-center justify-between">
                  <div className="flex items-center space-x-4">
                    <h3 className="font-semibold text-gray-900">{selectedMarket} K线图</h3>
                    <div className="flex items-center space-x-2 text-sm text-gray-600">
                      <span>时间周期:</span>
                      <span className="font-medium text-brand-600">{selectedTimeframe}</span>
                    </div>
                  </div>
                  <div className="flex items-center space-x-2">
                    <button 
                      onClick={() => setShowAIAssistant(!showAIAssistant)}
                      className={`p-1 rounded hover:bg-gray-200 transition-colors ${showAIAssistant ? 'bg-blue-100 text-blue-600' : ''}`}
                      title="AI指标助手"
                    >
                      <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9.75 17L9 20l-1 1h8l-1-1-.75-3M3 13h18M5 17h14a2 2 0 002-2V5a2 2 0 00-2-2H5a2 2 0 00-2 2v10a2 2 0 002 2z" />
                      </svg>
                    </button>
                    <button 
                      className="p-1 rounded hover:bg-gray-200 transition-colors"
                      title="全屏"
                    >
                      <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 8V4m0 0h4M4 4l5 5m11-1V4m0 0h-4m4 0l-5 5M4 16v4m0 0h4m-4 0l5-5m11 5l-5-5m5 5v-4m0 4h-4" />
                      </svg>
                    </button>
                    <button 
                      className="p-1 rounded hover:bg-gray-200 transition-colors"
                      title="设置"
                    >
                      <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M10.325 4.317c.426-1.756 2.924-1.756 3.35 0a1.724 1.724 0 002.573 1.066c1.543-.94 3.31.826 2.37 2.37a1.724 1.724 0 001.065 2.572c1.756.426 1.756 2.924 0 3.35a1.724 1.724 0 00-1.066 2.573c.94 1.543-.826 3.31-2.37-2.37a1.724 1.724 0 00-2.572 1.065c-.426 1.756-2.924 1.756-3.35 0a1.724 1.724 0 00-2.573-1.066c-1.543.94-3.31-.826-2.37-2.37a1.724 1.724 0 00-1.065-2.572c-1.756-.426-1.756-2.924 0-3.35a1.724 1.724 0 001.066-2.573c-.94-1.543.826-3.31 2.37-2.37.996.608 2.296.07 2.572-1.065z" />
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 12a3 3 0 11-6 0 3 3 0 016 0z" />
                      </svg>
                    </button>
                  </div>
                </div>
                
                {/* 绘图工具栏 - 基于原型设计 */}
                <div className="px-3 py-2 bg-white border-b border-gray-100 flex items-center justify-between">
                  <div className="flex items-center space-x-1">
                    <div className="flex items-center space-x-1 mr-4">
                      <span className="text-xs text-gray-500">绘图工具：</span>
                      <button
                        onClick={() => setActiveDrawingTool(activeDrawingTool === 'cursor' ? null : 'cursor')}
                        className={`p-1 rounded text-xs hover:bg-gray-100 ${activeDrawingTool === 'cursor' ? 'bg-blue-100 text-blue-600' : 'text-gray-600'}`}
                        title="光标"
                      >
                        <svg className="w-3 h-3" fill="currentColor" viewBox="0 0 20 20">
                          <path fillRule="evenodd" d="M6.672 1.911a1 1 0 10-1.932.518l.259.966a1 1 0 001.932-.518l-.26-.966zM2.429 4.74a1 1 0 10-.517 1.932l.966.259a1 1 0 00.517-1.932l-.966-.26zm8.814-.569a1 1 0 00-1.415-1.414l-.707.707a1 1 0 101.415 1.415l.707-.708zm-7.071 7.072l.707-.707A1 1 0 003.465 9.12l-.708.707a1 1 0 001.415 1.415zm3.2-5.171a1 1 0 00-1.3 1.3l4 10a1 1 0 001.823.075l1.38-2.759 3.018 3.02a1 1 0 001.414-1.415l-3.019-3.02 2.76-1.379a1 1 0 00-.076-1.822l-10-4z" clipRule="evenodd" />
                        </svg>
                      </button>
                      
                      <button
                        onClick={() => setActiveDrawingTool(activeDrawingTool === 'line' ? null : 'line')}
                        className={`p-1 rounded text-xs hover:bg-gray-100 ${activeDrawingTool === 'line' ? 'bg-blue-100 text-blue-600' : 'text-gray-600'}`}
                        title="直线"
                      >
                        <svg className="w-3 h-3" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 20l16-16" />
                        </svg>
                      </button>
                      
                      <button
                        onClick={() => setActiveDrawingTool(activeDrawingTool === 'rectangle' ? null : 'rectangle')}
                        className={`p-1 rounded text-xs hover:bg-gray-100 ${activeDrawingTool === 'rectangle' ? 'bg-blue-100 text-blue-600' : 'text-gray-600'}`}
                        title="矩形"
                      >
                        <svg className="w-3 h-3" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16v12H4z" />
                        </svg>
                      </button>
                      
                      <button
                        onClick={() => setActiveDrawingTool(activeDrawingTool === 'circle' ? null : 'circle')}
                        className={`p-1 rounded text-xs hover:bg-gray-100 ${activeDrawingTool === 'circle' ? 'bg-blue-100 text-blue-600' : 'text-gray-600'}`}
                        title="圆形"
                      >
                        <svg className="w-3 h-3" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                          <circle cx="12" cy="12" r="8" />
                        </svg>
                      </button>
                      
                      <button
                        onClick={() => setActiveDrawingTool(activeDrawingTool === 'fibonacci' ? null : 'fibonacci')}
                        className={`p-1 rounded text-xs hover:bg-gray-100 ${activeDrawingTool === 'fibonacci' ? 'bg-blue-100 text-blue-600' : 'text-gray-600'}`}
                        title="斐波那契回调"
                      >
                        <span className="text-xs font-mono">Fib</span>
                      </button>
                      
                      <button
                        onClick={() => setActiveDrawingTool(activeDrawingTool === 'text' ? null : 'text')}
                        className={`p-1 rounded text-xs hover:bg-gray-100 ${activeDrawingTool === 'text' ? 'bg-blue-100 text-blue-600' : 'text-gray-600'}`}
                        title="文本"
                      >
                        <span className="text-xs font-bold">T</span>
                      </button>
                    </div>
                    
                    <div className="flex items-center space-x-1 text-xs">
                      <span className="text-gray-500">指标：</span>
                      <button className="px-2 py-1 bg-gray-100 rounded hover:bg-gray-200 text-gray-600">MA</button>
                      <button className="px-2 py-1 bg-gray-100 rounded hover:bg-gray-200 text-gray-600">MACD</button>
                      <button className="px-2 py-1 bg-gray-100 rounded hover:bg-gray-200 text-gray-600">RSI</button>
                      <button className="px-2 py-1 bg-gray-100 rounded hover:bg-gray-200 text-gray-600">BOLL</button>
                    </div>
                  </div>
                  
                  <div className="flex items-center space-x-2">
                    <button
                      onClick={() => setActiveDrawingTool(null)}
                      className="px-2 py-1 text-xs bg-gray-100 rounded hover:bg-gray-200 text-gray-600"
                      title="清除绘图"
                    >
                      清除
                    </button>
                    <button
                      className="px-2 py-1 text-xs bg-gray-100 rounded hover:bg-gray-200 text-gray-600"
                      title="撤销"
                    >
                      撤销
                    </button>
                  </div>
                </div>
                
                {/* K线图表容器 */}
                <div ref={chartRef} className="w-full h-[calc(100%-120px)]" />
              </div>
            </div>

            {/* 右侧：交易面板和订单簿 */}
            <div className="col-span-4 space-y-4">
              {/* 交易面板 */}
              <div className="bg-white border border-gray-100 rounded-xl shadow-sm">
                <div className="p-4 border-b border-gray-100">
                  <h3 className="font-semibold text-gray-900">现货交易</h3>
                  {!isPremium && (
                    <div className="mt-2 p-2 bg-yellow-50 border border-yellow-200 rounded text-xs text-yellow-700">
                      <div className="flex items-center space-x-1">
                        <svg className="w-3 h-3" fill="currentColor" viewBox="0 0 20 20">
                          <path fillRule="evenodd" d="M8.257 3.099c.765-1.36 2.722-1.36 3.486 0l5.58 9.92c.75 1.334-.213 2.98-1.742 2.98H4.42c-1.53 0-2.493-1.646-1.743-2.98l5.58-9.92zM11 13a1 1 0 11-2 0 1 1 0 012 0zm-1-8a1 1 0 00-1 1v3a1 1 0 002 0V6a1 1 0 00-1-1z" clipRule="evenodd" />
                        </svg>
                        <span>实盘交易需要高级版本</span>
                      </div>
                    </div>
                  )}
                </div>
                
                <div className="p-4">
                  {/* 买卖切换 */}
                  <div className="flex mb-4">
                    <button 
                      onClick={() => handleTradeTypeChange('buy')}
                      className={`flex-1 py-2 text-center rounded-l-lg font-medium transition-colors ${
                        tradingForm.side === 'buy'
                          ? 'bg-green-100 text-green-700'
                          : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                      }`}
                    >
                      买入
                    </button>
                    <button 
                      onClick={() => handleTradeTypeChange('sell')}
                      className={`flex-1 py-2 text-center rounded-r-lg font-medium transition-colors ${
                        tradingForm.side === 'sell'
                          ? 'bg-red-100 text-red-700'
                          : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                      }`}
                    >
                      卖出
                    </button>
                  </div>
                  
                  {/* 订单类型 */}
                  <div className="flex mb-4 text-sm">
                    <button 
                      onClick={() => handleOrderTypeChange('limit')}
                      className={`px-3 py-1 rounded-l border ${
                        tradingForm.order_type === 'limit'
                          ? 'bg-brand-50 text-brand-700 border-brand-200'
                          : 'bg-gray-50 text-gray-700 border-gray-200 hover:bg-gray-100'
                      }`}
                    >
                      限价单
                    </button>
                    <button 
                      onClick={() => handleOrderTypeChange('market')}
                      className={`px-3 py-1 rounded-r border-t border-r border-b ${
                        tradingForm.order_type === 'market'
                          ? 'bg-brand-50 text-brand-700 border-brand-200'
                          : 'bg-gray-50 text-gray-700 border-gray-200 hover:bg-gray-100'
                      }`}
                    >
                      市价单
                    </button>
                  </div>

                  <div className="space-y-3">
                    {tradingForm.order_type === 'limit' && (
                      <div>
                        <label className="block text-sm text-gray-600 mb-1">价格 (USDT)</label>
                        <div className="relative">
                          <input 
                            type="text" 
                            value={tradingForm.price}
                            onChange={(e) => handlePriceChange(e.target.value)}
                            placeholder={currentPrice.replace(',', '')}
                            className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-brand-500 focus:border-transparent"
                          />
                          <button 
                            onClick={() => handlePriceChange(currentPrice.replace(',', ''))}
                            className="absolute right-2 top-1/2 transform -translate-y-1/2 text-xs text-brand-600 hover:text-brand-700"
                          >
                            最新价
                          </button>
                        </div>
                      </div>
                    )}
                    
                    <div>
                      <label className="block text-sm text-gray-600 mb-1">
                        数量 ({selectedMarket.split('/')[0]})
                      </label>
                      <input 
                        type="text" 
                        value={tradingForm.quantity}
                        onChange={(e) => handleQuantityChange(e.target.value)}
                        placeholder="0.001"
                        className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-brand-500 focus:border-transparent"
                      />
                      {/* 快速数量选择 */}
                      <div className="flex space-x-2 mt-2">
                        {['25%', '50%', '75%', '100%'].map((percent) => (
                          <button 
                            key={percent}
                            className="flex-1 py-1 text-xs border border-gray-200 rounded hover:bg-gray-50 transition-colors"
                            onClick={() => {
                              // TODO: 根据可用余额计算
                              const mockBalance = 1000 // 模拟余额
                              const percentValue = parseInt(percent) / 100
                              const calculatedAmount = (mockBalance * percentValue).toFixed(2)
                              handleAmountChange(calculatedAmount)
                            }}
                          >
                            {percent}
                          </button>
                        ))}
                      </div>
                    </div>
                    
                    <div>
                      <label className="block text-sm text-gray-600 mb-1">金额 (USDT)</label>
                      <input 
                        type="text" 
                        value={tradingForm.amount}
                        onChange={(e) => handleAmountChange(e.target.value)}
                        placeholder="43.25"
                        className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-brand-500 focus:border-transparent"
                      />
                    </div>
                    
                    <div className="flex justify-between text-xs text-gray-500 pt-2">
                      <span>
                        可用: {accounts[selectedExchange]?.balance?.USDT?.free?.toFixed(2) || '0.00'} USDT
                      </span>
                      <span>手续费: ~0.1%</span>
                    </div>
                    
                    <button 
                      onClick={handlePlaceOrder}
                      disabled={!isPremium || placingOrder || isLoading}
                      className={`w-full py-3 rounded-lg text-white font-medium transition-all ${
                        tradingForm.side === 'buy'
                          ? 'bg-green-500 hover:bg-green-600 disabled:bg-gray-300'
                          : 'bg-red-500 hover:bg-red-600 disabled:bg-gray-300'
                      } disabled:cursor-not-allowed`}
                    >
                      {!isPremium ? '升级以启用交易' : 
                       placingOrder ? '处理中...' :
                       `${tradingForm.side === 'buy' ? '买入' : '卖出'} ${selectedMarket.split('/')[0]}`}
                    </button>
                  </div>
                </div>
              </div>

              {/* 订单簿 */}
              <div className="bg-white border border-gray-100 rounded-xl shadow-sm">
                <div className="p-3 border-b border-gray-100">
                  <h3 className="font-semibold text-gray-900">订单簿</h3>
                </div>
                
                <div className="p-3">
                  {/* 表头 */}
                  <div className="grid grid-cols-3 gap-2 text-xs font-medium text-gray-500 mb-2">
                    <span>价格(USDT)</span>
                    <span className="text-center">数量(BTC)</span>
                    <span className="text-right">累计(BTC)</span>
                  </div>
                  
                  {/* 卖单 */}
                  <div className="space-y-1 mb-3">
                    {[
                      { price: '43,285.00', amount: '0.025', total: '0.850' },
                      { price: '43,280.00', amount: '0.048', total: '0.825' },
                      { price: '43,275.00', amount: '0.032', total: '0.777' },
                      { price: '43,270.00', amount: '0.056', total: '0.745' },
                      { price: '43,265.00', amount: '0.041', total: '0.689' },
                    ].map((order, index) => (
                      <div key={`sell-${index}`} className="grid grid-cols-3 gap-2 text-xs hover:bg-red-50 rounded px-1 py-0.5 cursor-pointer">
                        <span className="text-red-500 font-medium">{order.price}</span>
                        <span className="text-gray-700 text-center">{order.amount}</span>
                        <span className="text-gray-500 text-right">{order.total}</span>
                      </div>
                    ))}
                  </div>
                  
                  {/* 当前价格 */}
                  <div className="py-2 text-center border-t border-b border-gray-100 my-3">
                    <div className={`text-lg font-bold ${
                      priceChangePercent >= 0 ? 'text-green-600' : 'text-red-600'
                    }`}>
                      {currentPrice}
                    </div>
                    <div className="text-xs text-gray-500">≈ ${currentPrice.replace(',', '')}</div>
                  </div>
                  
                  {/* 买单 */}
                  <div className="space-y-1">
                    {[
                      { price: '43,240.00', amount: '0.067', total: '0.648' },
                      { price: '43,235.00', amount: '0.089', total: '0.581' },
                      { price: '43,230.00', amount: '0.045', total: '0.492' },
                      { price: '43,225.00', amount: '0.073', total: '0.447' },
                      { price: '43,220.00', amount: '0.038', total: '0.374' },
                    ].map((order, index) => (
                      <div key={`buy-${index}`} className="grid grid-cols-3 gap-2 text-xs hover:bg-green-50 rounded px-1 py-0.5 cursor-pointer">
                        <span className="text-green-500 font-medium">{order.price}</span>
                        <span className="text-gray-700 text-center">{order.amount}</span>
                        <span className="text-gray-500 text-right">{order.total}</span>
                      </div>
                    ))}
                  </div>
                </div>
              </div>
              
              {/* 我的订单 */}
              <div className="bg-white border border-gray-100 rounded-xl shadow-sm">
                <div className="p-3 border-b border-gray-100">
                  <h3 className="font-semibold text-gray-900">我的订单</h3>
                </div>
                
                <div className="p-3">
                  {activeOrders.length > 0 ? (
                    <div className="space-y-2">
                      {/* 表头 */}
                      <div className="grid grid-cols-4 gap-2 text-xs font-medium text-gray-500 pb-2 border-b">
                        <span>交易对</span>
                        <span>类型</span>
                        <span>价格</span>
                        <span>状态</span>
                      </div>
                      
                      {/* 订单列表 */}
                      {activeOrders.slice(0, 5).map((order) => (
                        <div key={order.id} className="grid grid-cols-4 gap-2 text-xs py-1 hover:bg-gray-50 rounded">
                          <span className="font-medium">{order.symbol}</span>
                          <span className={`font-medium ${
                            order.side === 'buy' ? 'text-green-600' : 'text-red-600'
                          }`}>
                            {order.side === 'buy' ? '买入' : '卖出'}
                          </span>
                          <span>{order.price || '市价'}</span>
                          <span className={`text-xs px-1 py-0.5 rounded ${
                            order.status === 'filled' ? 'bg-green-100 text-green-700' :
                            order.status === 'open' ? 'bg-blue-100 text-blue-700' :
                            order.status === 'canceled' ? 'bg-gray-100 text-gray-700' :
                            'bg-yellow-100 text-yellow-700'
                          }`}>
                            {order.status === 'filled' ? '已成交' :
                             order.status === 'open' ? '挂单中' :
                             order.status === 'canceled' ? '已取消' : '处理中'}
                          </span>
                        </div>
                      ))}
                    </div>
                  ) : (
                    <div className="text-center py-8 text-gray-400">
                      <svg className="w-12 h-12 mx-auto mb-3" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1} d="M9 5H7a2 2 0 00-2 2v10a2 2 0 002 2h8a2 2 0 002-2V7a2 2 0 00-2-2h-2M9 5a2 2 0 002 2h2a2 2 0 002-2M9 5a2 2 0 012-2h2a2 2 0 012 2" />
                      </svg>
                      <p className="text-sm">暂无订单</p>
                      {!isPremium && (
                        <p className="text-xs text-yellow-600 mt-1">升级账户后可查看订单</p>
                      )}
                    </div>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
        
        {/* AI指标助手对话面板 - 基于原型设计 */}
        {showAIAssistant && (
          <div className="border-t border-gray-200 bg-white">
            <div className="p-4">
              <div className="flex items-center justify-between mb-4">
                <h3 className="text-lg font-semibold" style={{ color: '#1a3d7c' }}>
                  🤖 AI指标助手
                </h3>
                <button
                  onClick={() => setShowAIAssistant(false)}
                  className="text-gray-400 hover:text-gray-600"
                >
                  <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                  </svg>
                </button>
              </div>
              
              <div className="grid grid-cols-4 gap-4 max-h-64 overflow-hidden">
                {/* AI对话区域 */}
                <div className="col-span-3 flex flex-col">
                  <div className="flex-1 bg-gray-50 rounded-lg p-4 mb-3 max-h-40 overflow-y-auto">
                    <div className="space-y-3">
                      {/* AI消息示例 */}
                      <div className="flex">
                        <div className="w-8 h-8 bg-blue-100 rounded-full flex items-center justify-center mr-3 flex-shrink-0">
                          <span className="text-blue-600 text-sm">🤖</span>
                        </div>
                        <div className="bg-white rounded-lg px-3 py-2 shadow-sm border">
                          <p className="text-sm text-gray-800">
                            根据当前BTC/USDT价格走势分析，RSI指标显示超买信号，建议关注回调机会。当前价格在布林带上轨附近，可能面临阻力。
                          </p>
                          <div className="text-xs text-gray-500 mt-1">刚刚</div>
                        </div>
                      </div>
                      
                      {/* 用户消息示例 */}
                      <div className="flex justify-end">
                        <div className="bg-blue-600 text-white rounded-lg px-3 py-2 max-w-xs">
                          <p className="text-sm">分析一下MACD指标</p>
                          <div className="text-xs text-blue-200 mt-1">1分钟前</div>
                        </div>
                      </div>
                    </div>
                  </div>
                  
                  {/* 输入区域 */}
                  <div className="flex space-x-2">
                    <input
                      type="text"
                      value={aiMessage}
                      onChange={(e) => setAiMessage(e.target.value)}
                      placeholder="请描述您的交易问题或需要的指标分析..."
                      className="flex-1 border border-gray-200 bg-gray-50 rounded-lg px-3 py-2 text-sm focus:border-blue-600 focus:outline-none focus:bg-white"
                      onKeyPress={(e) => {
                        if (e.key === 'Enter') {
                          toast.success('AI分析功能开发中...')
                          setAiMessage('')
                        }
                      }}
                    />
                    <button
                      onClick={() => {
                        toast.success('AI分析功能开发中...')
                        setAiMessage('')
                      }}
                      className="bg-blue-600 text-white rounded-lg px-4 py-2 text-sm font-medium hover:bg-blue-700 transition-colors"
                    >
                      发送
                    </button>
                  </div>
                </div>
                
                {/* 快速建议区域 */}
                <div className="bg-gray-50 rounded-lg p-3">
                  <h4 className="text-sm font-medium text-gray-700 mb-3">💡 快速建议</h4>
                  <div className="space-y-2">
                    {[
                      '分析RSI指标',
                      '查看MACD走势',
                      '布林带分析',
                      '支撑阻力位',
                      '趋势线分析',
                      '交易信号提醒'
                    ].map((suggestion, index) => (
                      <button
                        key={index}
                        onClick={() => {
                          setAiMessage(suggestion)
                        }}
                        className="w-full text-left text-xs bg-white border border-gray-200 rounded px-2 py-1 hover:bg-gray-100 text-gray-700"
                      >
                        {suggestion}
                      </button>
                    ))}
                  </div>
                </div>
              </div>
            </div>
          </div>
        )}
    </>
  )
}

export default TradingPage